.hero{
    background-image: url(../../../../public/Images/bg.webp);
    background-size: cover;
    background-attachment: fixed;
    position: absolute;
    top:0;
    left: 0;
    z-index: -1;
    width: 96%;
    height: 57vh;
    padding-top: 20%;
    padding-right: 50px;
    color: #fff;
}
.hero #heading h3{
    color: #fff;
}
.hero #heading{
    text-align: left;
    margin: 0;
    padding: 0;
}
@media  screen and (max-width: 768px) {
    .margin{
        height: 40vh;
    }
    .hero{
        background-position: right;
        padding-top: 50%;
        height: 80vh;
    }
    .hero .row{
        width: 100%;
        
    }

    @media  screen and (max-width: 425px) {
        .margin{
            height: 30vh;
        }
        .hero{
            background-position: right;
            padding-top: 60%;
            height: 80vh;
            width:auto;
        }
        .hero .row{
            width: 100%;
        }   
}
}